import React, { Component } from 'react';
import { connect } from "react-redux";
import Sidebar from "react-sidebar";
import { Route, Switch, BrowserRouter } from "react-router-dom";
import { ConnectedRouter } from "connected-react-router";
import { PersistGate } from "redux-persist/integration/react";
import 'bootstrap/dist/css/bootstrap.min.css';

import { openSidebar } from "./actions/common";
import ErrorBoundary from './ErrorBoundary.js'
import routes from "./router-config";
import history from "./history";
import store from "./store";
import SidebarContent from "./components/sidebar";
import Header from "./containers/HeaderContainer";

import './global';
import './styles/core.scss';

class MainContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {

    };
  }

  componentWillMount() {
    const token = window.cookieHandler.get('token');
    if(!token && window.location.pathname !== "/") {
      history.push('/');
    } else if(token && window.location.pathname === "/") {
     history.push('/notice');
    }
  }

  render() {
    const { sidebarOpen } = this.state;
    const { isSidebarOpen, triggerSidebar } = this.props;
    const sidebar = <SidebarContent onSetSidebarOpen={triggerSidebar} />;
    return (
      <div className="app-container">
        <Sidebar
          sidebar={sidebar}
          open={isSidebarOpen}
          onSetOpen={triggerSidebar}
        >
          <div className="view-container">
            <Switch>
              {routes.map((route, index) => (
                <Route
                  key={index}
                  path={route.path}
                  component={route.component}
                  exact={route.exact}
                />
              ))}
            </Switch>
          </div>
        </Sidebar>
      </div>
    )
  }
}

const mapStateToProps = ({ common }) => ({
  isSidebarOpen: common.isSidebarOpen,
});
const mapDispatchToProps = dispatch => ({
  triggerSidebar: () => dispatch(openSidebar()),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MainContainer);
